.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.navli{
  list-style: none;
  padding-right: 10px;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 1em;
  font-weight: bold;
  color: skyblue;
  cursor: pointer;
}

#navbar{
  display: flex;
  background: darkblue;
  /* padding-bottom: 10px; */
}

.mainPara{
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 10px;
  text-align: justify;
}
.App-link {
  color: #61dafb;
  
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
